import { ISidebarItemsLayout } from '@ali/4ever-component';
import { ImagePluginConfig } from './types';
import zhLocale from '../../locales/zh_CN';

/**
 * 获取图片工具栏的布局
 * @returns
 */
export function getToolbarLayout() {
  return [{
    toolButtons: [{
      name: 'image.download',
    }, {
      name: 'image.reset',
    }, {
      name: 'image.border',
    }],
  }, {
    toolButtons: [{
      name: 'image.crop',
    }, {
      name: 'image.rotateReverse',
    }, {
      name: 'image.rotate',
    }],
  }];
}

export function getSidebarLayout(config: ImagePluginConfig): ISidebarItemsLayout {
  const { locale: zhImageLocale } = zhLocale.image;
  const { locale = zhImageLocale } = config;
  return [
    {
      renderTitle: () => locale.sizeAndStyle,
      items: [
        'image.sizeAndStyle',
      ],
      index: 1,
      key: 'imageSizeAndStyle',
    }, {
      renderTitle: () => locale.recommendStyle,
      items: [
        'image.recommendStyle',
      ],
      index: 5,
      key: 'imageRecommendStyle',
    }, {
      renderTitle: () => locale.alignment,
      items: [
        'alignment',
      ],
      index: 3,
      key: 'alignmentDefault',
    },
    // 添加链接的交互未优化，暂时不添加
    // {
    //   renderTitle: () => locale.addLink,
    //   items: [
    //     'link.setInlineHref',
    //   ],
    //   index: 7,
    // },
  ];
}

